.feature{
  padding: 105px 0 100px;
  @include tablet{
    padding: 70px 0;
  }
  @include mobile{
    padding: 10px 0;
  }
  .section-title{
    left: 9%;
    margin-bottom: 105px;
    @include tablet{
      left: 50%;
      transform: translateX(-50%);
    }
    @include mobile{
      margin-bottom: 40px;
    }
  }
  &-item{
    padding: 35px 20px;
    @include desktop{
      margin-bottom: 30px;
    }
    @include tablet{
      padding: 30px;
    }
    @include mobile{
      padding: 35px 25px;
      margin-bottom: 17px;
    }
    .icon{
      margin-bottom: 35px;
    }
    h3{
      font-size: 20px;
      margin-bottom: 20px;
    }
    p{
      margin-bottom: 0;
      line-height: 31px;
      @include mobile{
        font-size: 12px;
        line-height: 22px;
      }
    }
    &.cl-1{
      background: rgba(60, 97, 175, 0.05);
    }
    &.cl-2{
      background: rgba(255, 98, 66, 0.05);
    }
    &.cl-3{
      background: rgba(0, 201, 242, 0.05);
    }
    &.cl-4{
      background: rgba(255, 220, 9, 0.05);
    }
  }
}