.testimonial{
  padding: 100px 0 105px;
  @include tablet{
    padding: 70px 0;
  }
  @include mobile{
    padding: 50px 0;
  }
  .section-title{
    left: 31%;
    margin-bottom: 45px;
    @include tablet{
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &-slider{
    margin-top: 100px;
    position: relative;
    @include tablet{
      margin-top: 50px;
      margin-bottom: 100px;
    }
    &-item{
      .content{
        display: flex;
        .logo{
          margin: 0 25px;
          @include mobile{
            margin: 0 15px 0 18%;
          }
          @include mobile-xs{
            margin: 0 15px 0 9%;
          }
          img{
            height: auto;
            padding: 15px;
            background: $white;
            box-shadow: 0px 28px 34px rgba(211, 211, 211, 0.25);
            border-radius: 21px;
          }
        }
        .text-content{
          h3{
            font-size: 24px;
            line-height: 50px;
            margin-bottom: 0;
            @include mobile{
              font-size: 18px;
              line-height: 30px;
              margin-top: 12px;
            }
          }
          span{
            display: inline-block;
            color: #98A9C5;
            font-size: 17px;
            margin-bottom: 30px;
            @include mobile{
              font-size: 12px;
            }
          }
          p{
            font-size: 20px;
            line-height: 34px;
            margin-bottom: 0;
            @include mobile{
              text-align: center;
              margin: 0 0 0 -66%;
              font-size: 12px;
              line-height: 22px;
            }
          }
        }
      }
    }
    .slick-prev, .slick-next{
      position: absolute;
      width: 44px;
      height: 58px;
      transition: 0.3s ease;
      svg{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        path{
          transition: 0.3s ease;
        }
      }
      border: 1px solid $orange;
      border-radius: 13px;
      @include mobile{
        border-radius: 2px;
      }
      &.slick-disabled{
        pointer-events: none;
        border-color: #d5d5d5;
        svg{
          path{
            stroke: #d5d5d5;
          }
        }
      }
    }
    .slick-prev{
      top: unset;
      bottom: -43%;
      left: 20%;
      @include mobile{
        left: calc(50% - 30px);
        transform: translateX(-50%);
        bottom: -52%;
      }
    }
    .slick-next{
      top: unset;
      bottom: -43%;
      left: calc(20% + 60px);
      @include mobile{
        left: calc(50% + 30px);
        transform: translateX(-50%);
        bottom: -52%;
      }
    }
  }
  &-image-wrapper{
    display: flex;
  }
  &-image{
    margin: 0 -70px 0 -100px;
    @include desktop{
      margin: 0;
    }
    @include tablet{
      margin: 0 -80px;
    }
    @include mobile{
      text-align: center;
      img{
        width: 80%;
      }
      margin: 0 -15%;
    }
  }
  &-illustration{
    align-self: flex-end;
    @include mobile{
      transform: translateX(-20px);
    }
    svg{
      width: 147px;
      height: 801px;
      margin: -200px 0;
      @include mobile{
        width: 86px;
        height: 618px;
        .half-circle{
          fill: #FF6142;
        }
        .circle{
          path{
            &:first-child{
              fill: #0F9D58;
            }
          }
        }
        .square{
          fill: #00C9F2;
        }
      }
    }
  }
}