body{
  overflow-x: hidden;
}

@media (min-width: 1200px){
  .container {
      max-width: 1170px;
  }
}

.col-12{
  @include mobile{
    padding-left: 30px;
    padding-right: 30px;
  }
}

section{
  overflow: hidden;
}

.section-title{
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
  @include mobile{
    justify-content: center;
  }
  h2{
    text-align: right;
    padding-right: 80px;
    position: relative;
    margin-bottom: 0;
    &::before{
      position: absolute;
      content: '';
      width: 6px;
      height: 90%;
      top: 50%;
      right: 15px;
      background: $purple;
      transform: translate(50%, -50%);
      @include mobile{
        width: 3px;
        height: 100%;
      }
    }
    @include mobile{
      font-size: 20px;
      list-style: 25px;
      padding-right: 30px;
    }
  }
  p{
    margin-bottom: 0;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.25em;
    color: $magenta;
    @include mobile{
      font-size: 12px;
      list-style: 18px;
      letter-spacing: 0;
    }
  }
  &.right{
    flex-direction: row-reverse;
    @include mobile{
      flex-direction: row;
    }
    h2{
      text-align: left;
      padding-right: 0;
      padding-left: 80px;
      @include mobile{
        padding-left: 0;
        padding-right: 30px;
        text-align: right;
      }
      &::before{
        right: unset;
        left: 40px;
        @include mobile{
          left: unset;
          right: 15px;
        }
      }
    }
  }
}

.page-header{
  padding: 200px 0 100px;
  h2{
    margin-bottom: 20px;
  }
  p{
    line-height: 36px;
  }
}

.hide-mobile{
  @include mobile{
    display: none;
  }
}

.hide-desktop{
  display: none;
  @include mobile{
    display: block;
  }
}