body {
  overflow-x: hidden;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

@media (max-width: 575px) {
  .col-12 {
    padding-left: 30px;
    padding-right: 30px;
  }
}

section {
  overflow: hidden;
}

.section-title {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
}

@media (max-width: 575px) {
  .section-title {
    justify-content: center;
  }
}

.section-title h2 {
  text-align: right;
  padding-right: 80px;
  position: relative;
  margin-bottom: 0;
}

.section-title h2::before {
  position: absolute;
  content: '';
  width: 6px;
  height: 90%;
  top: 50%;
  right: 15px;
  background: #8659D5;
  transform: translate(50%, -50%);
}

@media (max-width: 575px) {
  .section-title h2::before {
    width: 3px;
    height: 100%;
  }
}

@media (max-width: 575px) {
  .section-title h2 {
    font-size: 20px;
    list-style: 25px;
    padding-right: 30px;
  }
}

.section-title p {
  margin-bottom: 0;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 0.25em;
  color: #00C9F2;
}

@media (max-width: 575px) {
  .section-title p {
    font-size: 12px;
    list-style: 18px;
    letter-spacing: 0;
  }
}

.section-title.right {
  flex-direction: row-reverse;
}

@media (max-width: 575px) {
  .section-title.right {
    flex-direction: row;
  }
}

.section-title.right h2 {
  text-align: left;
  padding-right: 0;
