.story{
  padding: 70px 0 100px;
  @include tablet{
    padding: 50px 0 80px;
  }
  @include mobile{
    padding: 15px 0;
  }
  .section-title{
    left: 20%;
    margin-bottom: 58px;
    @include tablet{
      left: 50%;
      transform: translateX(-50%);
    }
    @include mobile{
      margin-bottom: 45px;
    }
  }
  &-wrapper{
    display: flex;
    @include tablet{
      flex-direction: column;
    }
  }
  &-content{
    margin-left: 110px;
    @include tablet{
      margin-left: 0;
      text-align: center;
    }
    p{
      font-size: 22px;
      line-height: 40px;
      @include mobile{
        font-size: 12px;
        line-height: 22px;
      }
    }
  }
  &-illustration{
    margin-top: 40px;
    margin-left: 18px;
    @include tablet{
      margin-top: 0;
      margin-left: 0;
      margin-bottom: 30px;
    }
    svg{
      height: 546px;
      width: 246px;
      margin: -150px 0;
      @include tablet{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
      @include mobile{
        width: 183px;
        height: 483px;
        .right-circle{
          path{
            &:first-child{
              fill: #FFDC09;
            }
          }
        }
      }
    }
  }
}

.hiw{
  padding: 100px 0 115px;
  @include tablet{
    padding: 70px 0;
  }
  @include mobile{
    padding: 20px 0;
  }
  .section-title{
    right: 5%;
    @include tablet{
      position: relative;
      right: 50%;
      transform: translateX(50%);
      p{
        text-align: right;
      }
    }
    @include mobile{
      margin-bottom: 35px;
    }
  }
  &-wrapper{
    display: flex;
    @include tablet{
      flex-direction: column;
    }
  }
  &-content{
    margin-right: 140px;
    margin-left: 70px;
    @include tablet{
      margin-right: 0;
      margin-left: 0;
      text-align: center;
      order: 2;
    }
    p{
      font-size: 22px;
      line-height: 40px;
      @include mobile{
        font-size: 12px;
        line-height: 22px;
      }
    }
  }
  &-illustration{
    margin-right: 30px;
    @include tablet{
      margin-right: 0;
      margin-bottom: 50px;
    }
    @include mobile{
      margin-bottom: 20px;
    }
    svg{
      width: 160px;
      height: 472px;
      margin: -100px 0;
      @include tablet{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
      @include mobile{
        width: 96px;
        height: 363px;
      }
    }
  }
}

.tff{
  padding: 100px 0 80px;
  @include mobile{
    padding: 30px 0;
  }
  .section-title{
    margin-bottom: 60px;
    @include tablet{
      padding: 70px 0;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }
    @include mobile{
      padding: 0;
      margin-bottom: 40px;
    }
  }
  &-wrapper{
    display: flex;
    margin-left: 16.66%;
    @include tablet{
      margin-left: 0;
      flex-direction: column;
    }
  }
  &-content{
    margin-left: 75px;
    @include tablet{
      margin-left: 0;
    }
    p{
      font-size: 22px;
      line-height: 40px;
      margin-bottom: 20px;
      @include mobile{
        font-size: 12px;
        line-height: 22px;
        margin-bottom: 10px;
      }
    }
    ul{
      margin: 0;
      padding-left: 22px;
      @include mobile{
        padding-left: 20px;
      }
      li{
        font-size: 22px;
        line-height: 40px;
        @include mobile{
          font-size: 12px;
          line-height: 22px;
        }
      }
    }
  }
  &-illustration{
    position: relative;
    z-index: 1;
    margin-top: 10px;
    @include tablet{
      margin-top: 0;
      margin-bottom: 30px;
    }
    svg{
      width: 242px;
      height: 825px;
      margin: -200px 0;
      @include tablet{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
      @include mobile{
        width: 112px;
        height: 604px;
      }
    }
  }
}

.company{
  padding: 80px 0 100px;
  @include tablet{
    padding: 70px 0;
  }
  @include mobile{
    padding: 30px 0;
  }
  .section-title{
    right: 0;
    margin-bottom: 80px;
    @include tablet{
      right: 50%;
      transform: translateX(50%);
      p{
        text-align: right;
      }
    }
  }
  &-wrapper{
    display: flex;
    @include tablet{
      flex-direction: column;
    }
  }
  &-content{
    margin-right: 115px;
    margin-left: 74px;
    @include tablet{
      order: 2;
      margin-right: 0;
      margin-left: 0;
      text-align: center;
    }
    p{
      font-size: 22px;
      line-height: 40px;
      @include mobile{
        font-size: 12px;
        line-height: 22px;
      }
    }
  }
  &-illustration{
    position: relative;
    z-index: 1;
    margin-top: 5px;
    @include tablet{
      margin-top: 0;
      margin-bottom: 30px;
    }
    @include mobile{
      margin-bottom: 70px;
    }
    svg{
      width: 219px;
      height: 612px;
      margin: -150px 0;
      .left-circle{
        position: relative;
        z-index: 1;
      }
      @include tablet{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
      @include mobile{
        width: 155px;
        height: 449px;
      }
    }
  }
}