.footer{
  background: #FBFBFB;
  padding: 60px 0 0;
  @include mobile{
    padding: 40px 0 0;
  }
  &-content{
    img{
      margin-bottom: 35px;
    }
    p{
      &:last-child{
        color: #8798B3;
      }
      @include mobile{
        font-size: 12px;
        line-height: 22px;
      }
    }
    @include tablet{
      text-align: center;
    }
  }
  &-widget{
    margin-top: 10px;
    @include tablet{
      text-align: center;
    }
    @include mobile{
      margin-bottom: 15px;
    }
    h3{
      font-size: 24px;
      margin-bottom: 15px;
      @include mobile{
        font-size: 22px;
      }
    }
    ul{
      margin: 0;
      padding: 0;
      list-style: none;
      li{
        a{
          display: block;
          padding: 10px 0;
          color: $text-color;
          font-size: 18px;
          transition: 0.3s ease;
          @include mobile{
            font-size: 12px;
          }
          &:hover{
            text-decoration: none;
            color: $orange;
          }
          &.featured{
            color: $orange;
            font-weight: 700;
          }
        }
      }
    }
  }
  &-bottom-bar{
    padding: 30px 0;
    margin-top: 60px;
    border-top: 1px solid $border-color;
    @include mobile{
      margin-top: 10px;
      padding: 20px 0;
    }
    p{
      text-align: center;
      margin-bottom: 0;
      color: #9FACC0;
      @include mobile{
        font-size: 15px;
      }
    }
  }
}