.faq{
  padding: 100px 0 200px;
  @include tablet{
    padding: 70px 0 140px;
  }
  @include mobile{
    padding: 10px 0 50px;
  }
  .section-title{
    right: 5%;
    margin-bottom: 60px;
    @include tablet{
      right: 50%;
      transform: translateX(50%);
    }
    @include mobile{
      margin-bottom: 30px;
    }
  }
  &-wrapper{
    display: flex;
    @include tablet{
      flex-direction: column;
    }
  }
  &-illustration{
    align-self: flex-end;
    @include tablet{
      align-self: unset;
    }
    svg{
      width: 272px;
      height: 831px;
      margin: -200px 0;
      @include tablet{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
      @include mobile{
        width: 152px;
        height: 585px;
      }
    }
    @include mobile{
      margin-bottom: 35px;
    }
  }
  &-accordion{
    margin-left: 120px;
    width: 100%;
    @include tablet{
      margin-left: 0;
    }
    &-item{
      margin-bottom: 15px;
      position: relative;
      z-index: 1;
      width: 100%;
      .header{
        position: relative;
        h5{
          .btn{
            display: block;
            text-align: left;
            padding: 20px 30px;
            font-size: 20px;
            width: 100%;
            text-decoration: none;
            color: $white;
            white-space: unset;
            position: relative;
            background: #3F51B5;
            border-radius: 0;
            z-index: 1;
            @include mobile{
              font-size: 16px;
            }
            @include mobile{
              padding: 20px 60px 20px 30px;
            }
            &:before, &:after{
              content: '';
              position: absolute;
              width: 15px; height: 2px;
              top: 50%; right: 25px;
              background: $white;
              transform: translateY(-50%) rotate(0);
              transition: transform .5s ease;
              border-radius: 2px;
              overflow: hidden;
            }
            .circle{
              position: absolute;
              width: 30px;
              height: 30px;
              border-radius: 50%;
              background: rgba(255, 255, 255, 0.1);
              right: 18px;
              top: 50%;
              transform: translateY(-50%);
              z-index: -1;
            }
            &.collapsed{
              background: rgba(63, 81, 181, 0.05);
              color: $text-color-dark;
              .circle{
                background: #3F51B5;
                box-shadow: 0px 6px 15px rgba(63, 81, 181, 0.2);
              }
              &:after{
                transform: translateY(-50%) rotate(270deg);
              }
            }
          }
        }
      }
      .body{
        color: $white;
        font-size: 14px;
        line-height: 25px;
        padding: 0 40px 30px 30px;
        background: #3F51B5;
        font-size: 18px;
        line-height: 30px;
        @include mobile{
          font-size: 12px;
          line-height: 22px;
        }
      }
    }
  }
}